import React, { Component } from 'react';
import Footer from "../shared/Footer";
import HeroImage from "../shared/HeroImage";
import PortfolioButton from "../shared/PortfolioButton";
import { Link } from 'react-router-dom';
import CustomGallery from "./CustomGallery";
import BurgerMenu from './BurgerMenu'; // Import the BurgerMenu component

class Mobile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showGallery: false,
      isBurgerMenuOpen: false,
    };
  }

  enterPortfolio() {
    this.setState({ showGallery: true });
    const burgerToggle = document.getElementById("burger-menu-toggle-id");
    burgerToggle.style.visibility = 'visible'; // Use 'style.visibility' to set CSS property
  }

  toggleBurgerMenu() {
    this.setState((prevState) => ({ isBurgerMenuOpen: !prevState.isBurgerMenuOpen }));
  }

  closeBurgerMenu() {
    this.setState({ isBurgerMenuOpen: false });
  }

  handleClickOutside = (e) => {
    if (this.state.isBurgerMenuOpen) {
      const burgerMenuToggle = document.querySelector('.burger-menu-toggle');
      if (burgerMenuToggle && !burgerMenuToggle.contains(e.target)) {
        this.closeBurgerMenu();
      }
    }
  };

  // Add the click event listener when the component mounts
  componentDidMount() {
    document.addEventListener('click', this.handleClickOutside);
  }

  // Clean up the event listener when the component unmounts
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClickOutside);
  }

  render() {
    const gifContainerStyle = {
      position: "fixed",
      left: "50%",
      bottom: "35%",
      width: "100%",
      transform: "translateX(-50%)",
      textAlign: "center",
    };

    const footerStyle = {
      display: "block",
      position: "fixed",
      bottom: "10%",
      displaycameratag: "none",
    };

    const portfolioButtonStyle = {
      fontSize: 'large',
      color: '#dddd',
      position: 'fixed',
      fontWeight: 700,
      left: '50%',
      transform: 'translateX(-50%)',
      bottom: '50%',
    };

    const burgerMenuToggleStyle = {
      position: 'fixed',
      bottom: '0',  // Position at the bottom
      background: 'black',
      opacity: '85%',
      width: '100%',
      padding: '2%',
      visibility: 'hidden',
    };

    const { showGallery, isBurgerMenuOpen } = this.state;

    return (
      <div className="MobileTablet">
        {/* Burger menu toggle button */}
        <div id="burger-menu-toggle-id" className="burger-menu-toggle" style={burgerMenuToggleStyle} onClick={this.toggleBurgerMenu}>
          <span>&#9776;</span> {/* Add a burger menu icon here */}
        </div>

        {/* Burger menu component */}
        <BurgerMenu isOpen={isBurgerMenuOpen} onClose={this.toggleBurgerMenu} />

        {showGallery ? null : (
          <HeroImage gifSrc="/home/homepagehero.gif" gifContainerStyle={gifContainerStyle}/>
        )}
        {showGallery ? (
          <CustomGallery />
        ) : (
          <>
            <Link onClick={() => this.enterPortfolio()}>
              <PortfolioButton portfolioButtonStyle={portfolioButtonStyle} />
            </Link>
            <Footer footerStyle={footerStyle} displaycameratag="none"/>
          </>
        )}
      </div>
    );
  }
}

export default Mobile;
